@tailwind base;
@tailwind components;
@tailwind utilities;
@import "~loaders.css/src/animations/pacman.scss";
.loader-hidden {
  display: none;
}
.loader-active {
  display: block;
}

.loader-custom {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.pacman > div:first-of-type {
  width: 0;
  height: 0;
  border-right: 25px solid rgb(0 0 0 / 0%);
  border-top: 25px solid rgb(253 255 0);
  border-left: 25px solid rgb(253 255 0);
  border-bottom: 25px solid rgb(253 255 0);
  border-radius: 25px;
  animation: rotate_pacman_half_up 0.5s 0s infinite;
  position: relative;
  left: -30px;
}

.pacman > div:nth-child(2) {
  width: 0;
  height: 0;
  border-right: 25px solid rgb(0 0 0 / 0%);
  border-top: 25px solid rgb(253 255 0);
  border-left: 25px solid rgb(253 255 0);
  border-bottom: 25px solid rgb(253 255 0);
  border-radius: 25px;
  animation: rotate_pacman_half_down 0.5s 0s infinite;
  margin-top: -50px;
  position: relative;
  left: -30px;
}

.pacman > div:nth-child(3),
.pacman > div:nth-child(4),
.pacman > div:nth-child(5),
.pacman > div:nth-child(6) {
  background-color: rgb(0 0 0);
  border-radius: 100%;
  margin: 2px;
  width: 10px;
  height: 10px;
  position: absolute;
  transform: translate(0, -6.25px);
  top: 25px;
  left: 70px;
  z-index: -1;
}
